import React, { Component } from 'react';
import './FormAdd.css';

class FormAdd extends Component {
	constructor(props) {
		super(props);

		this.state = {
			'newItem': '',
			'addDisabled': true
		};

		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(event) {
		if (event.target.value !== '') {
			this.setState({addDisabled: false});
		}
		else {
			this.setState({addDisabled: true});
		}
		this.setState({newItem: event.target.value});
	}

	handleSubmit(event) {
		event.preventDefault();
		if (this.props.addItem) {
			this.props.addItem(this.state.newItem);
		}
		if (this.props.addChildItem) {
			this.props.addChildItem(this.props.parentId, this.state.newItem);
		}
		this.setState({newItem: '', addDisabled: true});
	}

	render() {
		return (
			<div className="add-form">
				{this.props.title && <h5>{this.props.title}</h5>}
				<form onSubmit={this.handleSubmit}>
					<input id="newitem" type="text" className="input-line" value={this.state.newItem} onChange={this.handleChange} />
					<input type="submit" value="+" disabled={this.state.addDisabled} />
				</form>
			</div>
			);
	}
}

export default FormAdd;



