import React, { Component } from 'react';
import './App.css';
import './Projects.css';
import ListBox from "./ListBox";
import FormAdd from './FormAdd';
import { Box } from "./App";

class Projects extends Component {
	constructor(props) {
		super(props);

		this.state = {
			'isCollapsed': false,
			'newProject': ''
		};

		this.toggleCollapse = this.toggleCollapse.bind(this);
	}

	toggleCollapse() {
		this.setState({isCollapsed: !this.state.isCollapsed});
	}

	render() {
		var projectTitle = this.props.projects.length < 2 ? this.props.projects.length + " Project" : this.props.projects.length + " Projects";
		return (
			<div className="menu-block">
				<h2>{projectTitle}<input type="submit" value={this.state.isCollapsed? String.fromCharCode(0x25BE) : String.fromCharCode(0x25B4)} onClick={() => this.toggleCollapse()} /></h2>
				<div className={this.state.isCollapsed? "disNon marg" : "disVis marg"}>
					<Box>
						<FormAdd newItem={this.state.newProject} title="Add new project" addItem={this.props.addProject} />
					</Box>
					{this.props.projects.map((project, index) =>
						<Box key={project.pid} >
							<input type="submit" value="x" className="top-right" onClick={() => this.props.delProject(project.pid)} />
							<h5 className="inl-b left truncate">{project.name}</h5><span className="low-c small right"> - {project.tasks.length} task(s)</span>
							<FormAdd type="task" addChildItem={this.props.addTask} parentId={project.pid} />
							<ListBox key={project.pid} index={index} project={project} addTask={this.props.addTask} delTask={this.props.delTask} handleInputChange={this.props.handleInputChange} />
						</Box>
					)}
				</div>
			</div>
		);
	}
}

export default Projects;



