import React, { Component } from 'react';
import { Box } from "./App";

class Dashboard extends Component {
	render() {
		return (
			<Box>
				<ul className="list-box">
					{this.props.tasks.map((task, index) =>
						<li key={task.tid} index={index}>
							{task.name}<input type="submit" value="x" className="remove right" onClick={() => this.props.delTask(task.tid, task.pid)} />
						</li>
					)}
				</ul>
			</Box>
		);
	}
}

export default Dashboard;



