import React, { Component } from 'react';
import './ListBox.css';

class ListBox extends Component {
	render() {
		return (
			<React.Fragment>
				<ul className="list-box">
					{this.props.project.tasks.map((task, index) =>
						<li key={task.tid} index={index}>
							<input type="submit" name={task.featured? "featured":"not-featured"} value="*" className={task.featured? "featured left":"neutral left"} onClick={() => this.props.handleInputChange(task.tid, this.props.project.pid)} />
							{task.name}
							<input type="submit" value="x" className="remove right" onClick={() => this.props.delTask(task.tid, this.props.project.pid)} />
						</li>
					)}
				</ul>
			</React.Fragment>
			);
	}
}

export default ListBox;



