import React, { Component } from 'react';
import './App.css';
import Dashboard from "./Dashboard";
import Projects from "./Projects";

class App extends Component {
	constructor(props) {
		super(props);

		var projects = (localStorage.getItem('projects') && localStorage.getItem('projects').length) ? JSON.parse(localStorage.getItem('projects')) : [];
		var proTracker = localStorage.getItem('proTracker') && localStorage.getItem('proTracker').length ? localStorage.getItem('proTracker') : 0;
		var tasTracker = localStorage.getItem('tasTracker') && localStorage.getItem('tasTracker').length ? localStorage.getItem('tasTracker') : 0;
		var dashboardTasks = [];
		for (var i=0; i<projects.length; i++) {
			for (var j=0; j<projects[i]['tasks'].length; j++) {
				if (projects[i]['tasks'][j].featured) {
					var task = {
						'pid': projects[i].pid,
						'tid': projects[i]['tasks'][j].tid,
						'name': projects[i]['tasks'][j].name,
						'featured': true
					}
					dashboardTasks.push(task);
				}
			}
		}

		this.state = {
			'projects': projects,
			'dashboardTasks': dashboardTasks,
			'proTracker': proTracker,
			'tasTracker': tasTracker,
			'version': '1.0',
			'message': '',
		}

		if (localStorage.getItem('version') !== this.state.version) {
			localStorage.clear();
			this.state.message = "Your previous data have been cleared with the release of a new version.";
			localStorage.setItem('version', this.state.version);
		}

		this.addProject = this.addProject.bind(this);
		this.addTask = this.addTask.bind(this);
		this.delProject = this.delProject.bind(this);
		this.delTask = this.delTask.bind(this);
		this.toggleCheckedInput = this.toggleCheckedInput.bind(this);
	}

	findCollection(type, id) {
		var list = this.state[type];
		for (var i=0; i<list.length; i++) {
			if (list[i][id.name] === id.value) {
				return list[i];
			}
		}

	}

	addItem(type, newItem, tracker) {
		var list = this.state[type];
		list.push(newItem);
		this.setState({type: list, [tracker.name]: tracker.value});
		localStorage.setItem(type, JSON.stringify(list));
		localStorage.setItem(tracker.name, JSON.stringify(tracker.value));
	}

	addChildItem(parentType, type, parentId, newItem, tracker) {
		var parents = this.state[parentType];
		for (var i=0; i<parents.length; i++) {
			if (parents[i][parentId.name] === parentId.value) {
				parents[i][type].push(newItem);
				this.setState({parentType: parents, [tracker.name]: tracker.value});
				localStorage.setItem(parentType, JSON.stringify(parents));
				localStorage.setItem(tracker.name, JSON.stringify(tracker.value));
				break;
			}
		}
	}

	delItem(type, id) {
		var list = this.state[type];
		for (var i=0; i<list.length; i++) {
			if (list[i][id.name] === id.value) {
				list.splice(i, 1);
				break;
			}
		}
		this.setState({type: list});
		localStorage.setItem(type, JSON.stringify(list));
	}

	delChildItem(parentType, type, parentId, id) {
		var parents = this.state[parentType];
		for (var i=0; i<parents.length; i++) {
			if (parents[i][parentId.name] === parentId.value) {
				var children = parents[i][type];
				for (var j=0; j<children.length; j++) {
					if (children[j][id.name] === id.value) {
						parents[i][type].splice(j, 1);
						break;
					}
				}
				break;
			}
		}
		this.setState({parentType: parents});
		localStorage.setItem(parentType, JSON.stringify(parents));
	}

	addProject(name) {
		var proTracker = this.state.proTracker;
		var newProject = {
			'pid': proTracker++,
			'name': name,
			'tasks': []
		};
		var tracker = {
			'name': 'proTracker',
			'value': proTracker
		}
		this.addItem('projects', newProject, tracker);
	}

	addTask(pid, name) {
		var id = {
			'name': 'pid',
			'value': pid,
		}
		var tasTracker = this.state.tasTracker;
		var newTask = {
			'tid': tasTracker++,
			'name': name,
			'featured': false
		}
		var tracker = {
			'name': 'tasTracker',
			'value': tasTracker
		}
		this.addChildItem('projects', 'tasks', id, newTask, tracker);
	}

	delProject(pid) {
		var id = {
			'name': 'pid',
			'value': pid
		}
		this.delItem('projects', id);

		var dashboardTasks = this.state.dashboardTasks;
		var dlength = dashboardTasks.length;
		var i = 0;

		while (i<dlength) {
			if (dashboardTasks[i]['pid'] === pid) {
				this.delItem('dashboardTasks', {'name': 'tid', 'value': dashboardTasks[i]['tid']});
				dlength--;
				i--;
			}
			i++;
		}
	}

	delTask(tid, pid) {
		var parentId = {
			'name': 'pid',
			'value': pid
		}
		var id = {
			'name': 'tid',
			'value': tid
		}
		this.delChildItem('projects', 'tasks', parentId, id);
		this.delItem('dashboardTasks', id);
	}

	toggleCheckedInput(tid, pid) {
		var projects = this.state.projects;
		var dashboardTasks = this.state.dashboardTasks;
		for (var i=0; i<projects.length; i++) {
			if (projects[i]['pid'] === pid) {
				var tasks = projects[i]['tasks'];
				for (var j=0; j<tasks.length; j++) {
					if (tasks[j]['tid'] === tid) {
						if (projects[i]['tasks'][j].featured) {
							for (var k=0; k<dashboardTasks.length; k++) {
								if (dashboardTasks[k]['tid'] === tid) {
									dashboardTasks.splice(k, 1);
									break;
								}
							}
						}
						else {
							projects[i]['tasks'][j]['pid'] = pid;
							dashboardTasks.push(projects[i]['tasks'][j]);
						}

						projects[i]['tasks'][j].featured = !projects[i]['tasks'][j].featured;

						break;
					}
				}
				break;
			}
		}

		this.setState({projects: projects});
		this.setState({dashboardTasks: dashboardTasks});
		localStorage.setItem('projects', JSON.stringify(projects));
	}

	render() {
		return (
			<div className="App">
			<main>
				<div className="contrast">{this.state.message}</div>
				{this.state.dashboardTasks.length?
					<Dashboard tasks={this.state.dashboardTasks} delTask={this.delTask} /> : <h5>Your dashboard is empty. Please add projects and tasks to be featured below.</h5>
				}
			</main>
			<Projects projects={this.state.projects} addProject={this.addProject} delProject={this.delProject} addTask={this.addTask} delTask={this.delTask} handleInputChange={this.toggleCheckedInput} />
			</div>
			);
	}
}

export default App;

function Box(props) {
	return (
		<div className="box">
			{props.children}
		</div>
	);
}

export { Box };



